import './App.css';
import { useEffect, useState } from "react";

let dateObj = new Date(2021, 4, 21, 20)

let hours = dateObj.getHours()

var greetings = ""

if (hours >= 1 && hours < 12) {
  greetings = "Good Morning!"
}
else if (hours >= 12 && hours < 17) {
  greetings = "Good Noon!"
}
else if (hours >= 17 && hours <= 19) {
  greetings = "Good Evening!"
}
else {
  greetings = "Good Night!"
}

const openSearch = () => {
  
  let query = document.getElementById("search").value;
  let searchURL = `https://www.google.com/search?q=${query}`;
  window.location.href = searchURL;
}


function App() {
  const userName = "Hussnain Ahmad"

  const [background, setBackground] = useState("");
  const [totalTime, setTotalTime] = useState(new Date());
  const [searchTerm, setSearchTerm] = useState("");

  function changeBackground() {
  let randomNumber = Math.round(Math.random() * 10);
  setBackground(`${randomNumber}.jpg`)
  alert("Background Number: " + randomNumber)
}


useEffect(() => {
  setInterval(() => setTotalTime(new Date()), 1000);
}, []);

  return (
    <div className="App" id="App">
    <div className="App_main">
      <h1 id="greetings">Hi { userName.split(" ")[0] }, <span id="greet">{ greetings }</span></h1>
    <h1 id="time">
    {totalTime.toLocaleString('en-GB', {
      hour: "2-digit",
      minute: "2-digit",
      second: "2-digit",
      hour12: true,
    })}
    </h1>
      <input type="text" name="" id="search" placeholder="What is your main focus for today? " />
      <input type="button" id="searchBtn" onClick={openSearch} value="Search" />
    </div>
      <button id="changeBackground" onClick={changeBackground}>Change Background</button>
    </div>
  );
}

export default App;
